@media screen and (min-width: 600px){
    .chat{
        width: 100%;
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        align-items: center;
       
    }

    .chat h4{
        text-align: center;
    }
    .chatList{
        width: 100%;
        height: auto;
  

        
    }
    .chat .message{
        width: 100%;
        display: flex;
        margin: 10px;
        box-sizing: border-box;
        overflow: scroll;
    
      
    }


.chat .message .tria-left{
    color: white;
  width: 0px;
  height: 0px;
  border: 10px solid black;
  border-top-color: transparent;
  border-right-color: hsla(218, 97%, 56%, 1);
  border-bottom-color: transparent;
  border-left-color: transparent;
  margin-top: 5px;
}

.chat .message .recta{
    width: max-content;
    background-color:hsla(218, 97%, 56%, 1);
    color: white;
    padding: 10px;
    border-radius: 5px;
    position: relative;
    box-sizing: border-box;
}
.chat .message .recta h6{
    margin: 0;
    padding: 0;
    margin-bottom: 20px;

}
.chat .typing{
    box-sizing: border-box;
    display: flex;
    position: absolute;
    bottom: 0;
    width: 100%;
}

.typeBox{
    width: 85%;
    height: 40px;
    box-sizing: border-box;
   

}

.sendBtn{
    padding: 5px;
    background-color: hsla(218, 97%, 56%, 1);
    color: white;
    border: none;
    flex: 1;
    height: 40px;
    box-sizing: border-box;
}

.chat .userIcon{
    width: 22px;
    height: 22px;
    border:1px solid #000000;
    border-radius: 50%;
}
.chat .userIcon img{
    width: 20px;
    height: 20px;
}


.groupName{
    height: 10%;
}
.chatList{
    height: 80%;
    overflow: scroll;
}
.sendMessage{
    width: 100%;
    height: 10%;
    position: relative;
}
}