@media screen and (min-width: 600px) {
    .support{
        width: 80%;
        height: 100%;
        margin-left: auto;
        box-sizing: border-box;

    }

    .support .banner{
        width: 100%;
        box-sizing: border-box;
        padding: 5%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .support .banner #search-box{
        width: 60%;
        height: auto;
        box-sizing: border-box;
        position: relative;
        padding: 0px;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
        margin: 10px;
        border-radius: 25px;
    }
    .support .banner #search-box input[type="text"]{
        width: 100%;
        height: 40px;
        border: none;
        padding: 0px;
        border-radius: 25px;
        margin: 0px;
    }

    .support .banner #search-box button{
        position: absolute;
        right: 0;
        top: 0;
        height: 40px;
        margin: 0px;
        border: none;
        background-color: #0B85B1;
        color: #f5f5f5;
        border-radius: 0px 25px 25px 25px;
        background: hsla(280, 84%, 41%, 1);

background: linear-gradient(90deg, hsla(280, 84%, 41%, 1) 0%, hsla(218, 97%, 56%, 1) 100%);

background: -moz-linear-gradient(90deg, hsla(280, 84%, 41%, 1) 0%, hsla(218, 97%, 56%, 1) 100%);

background: -webkit-linear-gradient(90deg, hsla(280, 84%, 41%, 1) 0%, hsla(218, 97%, 56%, 1) 100%);

filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#8711C1", endColorstr="#2472FC", GradientType=1 );
    }
    .card{
        display:flex;
        justify-content: space-around;
        background-color: #f5f5f5;
        margin: 10px;
        padding: 20px;
        align-items: center;
        border-radius: 10px;
        
    }
    .head{
        height: min-content;
        padding: 5px;
    }
    .card div{
        min-width: 20%;
        max-width: 20%;
        box-sizing: border-box;
    }
    .card button{
        
        color: #f5f5f5;
        border: none;
        padding: 5px;
        background: hsla(280, 84%, 41%, 1);

background: linear-gradient(90deg, hsla(280, 84%, 41%, 1) 0%, hsla(218, 97%, 56%, 1) 100%);

background: -moz-linear-gradient(90deg, hsla(280, 84%, 41%, 1) 0%, hsla(218, 97%, 56%, 1) 100%);

background: -webkit-linear-gradient(90deg, hsla(280, 84%, 41%, 1) 0%, hsla(218, 97%, 56%, 1) 100%);

filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#8711C1", endColorstr="#2472FC", GradientType=1 );
    }
}